.App-title {
  /* color: #4b8bf9; */
  color: #25072f;
  /* color: #eded00; */
  font-size: 80px;
  margin: 0;
  font-family: 'Exo', sans-serif;
}

header {
  /* height: 100%; */
  margin: 30px 50px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
}

.app-intro {
  color: #4b8bf9;
}

.signin-container {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  -ms-flex-align: start;
      align-items: flex-start;
  -ms-flex-pack: justify;
      justify-content: space-between;
  margin: auto auto;
  /* background-color: #71eeb8; */
  background: rgb(0,211,222);
  background: -webkit-linear-gradient(150deg, rgba(0,211,222,1) 0%, rgba(113,238,184,1) 50%, rgba(0,255,130,1) 100%);
  background: -o-linear-gradient(150deg, rgba(0,211,222,1) 0%, rgba(113,238,184,1) 50%, rgba(0,255,130,1) 100%);
  background: linear-gradient(300deg, rgba(0,211,222,1) 0%, rgba(113,238,184,1) 50%, rgba(0,255,130,1) 100%);
  height: 100%;
}

.signin-section {
  height: 100%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-align: end;
      align-items: flex-end;
  margin: 0 50px;
}

.signin-form {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-align: center;
      align-items: center;
  /* max-width: 5em; */
  font-size: 50px;
  margin-top: 30px;
  /* border: 2px solid #4b8bf9; */
  border-radius: 10px;

}

.signin-form > input, .signin-form > button {
  margin: 10px;
  /* border: 1px solid #4b8bf9; */
  border: 1px solid #1da1f2;
  border-radius: 5px;
  font-size: 18px;
  /* color: grey; */
  /* color: #25072f; */
  padding: 12px;
}

input:focus {
  /* box-shadow: 1px 1px 5px #1c62da; */

}

.signin-field {
  outline: none;
}

.signin-field::-webkit-input-placeholder {
  color: #a8a2a2;
}

.signin-field:-ms-input-placeholder {
  color: #a8a2a2;
}

.signin-field::-ms-input-placeholder {
  color: #a8a2a2;
}

.signin-field::placeholder {
  color: #a8a2a2;
}

.signin-button {
  width: 100px;
  /* color: #4b8bf9; */
  color: #ffffff;
  cursor: pointer;
  outline: none;
  /* background-color: #cddeff; */
  background-color: #1da1f2;
  -webkit-transition: 0.3s;
  -o-transition: 0.3s;
  transition: 0.3s;
}

.signin-button:hover, .signin-button:focus {
  /* background-color: #b0c8f6; */
  background-color: #006dbf;
  -webkit-transition: 0.3s;
  -o-transition: 0.3s;
  transition: 0.3s;
  /* color: white; */
}

input:invalid:not(:focus) {
  border: 1px solid #ff4a22;
}

.error-message {
  font-size: 16px;
  margin: 0;
  color: red;
}

.visually-hidden {
  border: 0;
  clip: rect(0 0 0 0);
  height: 1px;
  margin: -1px;
  overflow: hidden;
  padding: 0;
  position: absolute;
  width: 1px;
}

.register-memo {
  color: #ffffff;
}

.register-link {
  text-decoration: none;
  font-weight: bold;
  color: #4b8bf9;
  background-color: var(--background);
  padding: 15px;
  border-radius: 10px;
  /* border: 2px solid #4b8bf9; */
}

.link-text {
  margin: 0;
  text-decoration: none;
}

a {
  text-decoration: none;
}

.register-link:hover {
  color: #1c62da;
}

.hype {
  color: #ffffff;
  font-size: 60px;
  margin: 30px 0;
}

.register-section {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-align: center;
      align-items: center;
  margin-top: 100px;
}



