
.button-con {
  /* cursor: default;
  position: absolute;
  top: 50%;
  left: 50%;
  margin-left: -76px;
  margin-top: -18px; */
  padding: 10px;
  margin-right: 20px;
}

#dayIcon {
  position: relative;
  /* width: 26px; */
  width: 1.5em;
  height: 1.5em;
  /* height: 26px; */
  top: -1.5px;
  margin: 0 7px;
  /* fill: #9caec0; */
  fill: #f1d339;
}

#nightIcon {
  position: relative;
  /* width: 26px; */
  /* height: 26px; */
  width: 1.5em;
  height: 1.5em;
  top: -1.5px;
  margin: 0 7px;
  /* fill: #9caec0; */
  fill: #a5b7c8;
}

#dayIcon, #nightIcon {
  cursor: pointer;
}

.toggle {
  display: none;
}

.toggle, .toggle:after, .toggle:before, .toggle *, .toggle *:after, .toggle *:before, .toggle + .toggle-button {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}

.toggle::-moz-selection, .toggle:after::-moz-selection, .toggle:before::-moz-selection, .toggle *::-moz-selection, .toggle *:after::-moz-selection, .toggle *:before::-moz-selection, .toggle + .toggle-button::-moz-selection {
  background: none;
}

.toggle::selection, .toggle:after::selection, .toggle:before::selection, .toggle *::selection, .toggle *:after::selection, .toggle *:before::selection, .toggle + .toggle-button::selection {
  background: none;
}

.toggle + .toggle-button {
  outline: 0;
  display: inline-block;
  width: 3em;
  height: 1.5em;
  position: relative;
  cursor: pointer;
  border: 2px solid #333;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}

.toggle + .toggle-button:after, .toggle + .toggle-button:before {
  position: relative;
  display: block;
  content: "";
  width: 50%;
  height: 100%;
}

.toggle + .toggle-button:after {
  left: 0;
}

.toggle + .toggle-button:before {
  display: none;
}

.toggle:checked + .toggle-button:after {
  left: 50%;
}

.toggle + .toggle-button {
  padding: 2px;
  -webkit-transition: all .2s ease;
  -o-transition: all .2s ease;
  transition: all .2s ease;
  /* border: 2px solid rgba(156, 174, 192, 0.27); */
  border: 2px solid rgba(156, 174, 192, 0.5);
  border-radius: 2em;
}

.toggle + .toggle-button:after {
  -webkit-transition: all .2s ease;
  -o-transition: all .2s ease;
  transition: all .2s ease;
  /* background: rgba(156, 174, 192, 0.27); */
  background: rgba(156, 174, 192, 0.5);
  content: "";
  border-radius: 1em;
}

.toggle:checked + .toggle-button:after {
  left: 50%;
}



