.new-comment-form {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-align: start;
      align-items: flex-start;
  padding: 10px;
  margin-top: 30px;
  /* margin: 10px 10px 20px 10px; */
}

.visually-hidden {
  border: 0;
  clip: rect(0 0 0 0);
  height: 1px;
  margin: -1px;
  overflow: hidden;
  padding: 0;
  position: absolute;
  width: 1px;
}

.new-comment-button {
  font-size: 18px;
  border: 2px solid #4b8bf9;
  border-radius: 30px;
  background-color: #4b8bf9;
  color: #ffffff;
  outline: none;
  cursor: pointer;
  padding: 5px 10px;
  margin: 5px 0;
  -webkit-transition: 0.2s;
  -o-transition: 0.2s;
  transition: 0.2s;
}

.new-comment-button:hover, .new-comment-button:focus {
  border-color: #1c62da;
  background-color: #1c62da;
  -webkit-transition: 0.2s;
  -o-transition: 0.2s;
  transition: 0.2s;
}

.new-comment-field {
  border: 2px solid #4b8bf9;
  border-radius: 8px;
  outline: none;
  padding: 10px;
  resize: none;
  font-size: 18px;
  margin: 5px 0;
  width: 500px;
}

.new-comment-field:focus {
  border-color: #1c62da;
  /* box-shadow: 0 0 0 1px rgb(77, 144, 254); */
}

.prompt {
  font-size: 18px;
  /* color: #25072f; */
}

.alert-box {
  color: red;
}

.invalid {
  border: 2px solid red !important;
}



