.vote-container {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-align: center;
      align-items: center;
  padding: 5px;
  min-width: 50px;
}

.vote-button {
  border: none;
  font-size: 20px;
  outline: none;
  cursor: pointer;
  padding: 0;
  opacity: 0.5;
  background-color: inherit;
}

.vote-button:hover {
  opacity: 1;
}

.vote-display {
  padding: 5px;
}

.voted-up {
  opacity: 1;
  color: #03d703;
}

.voted-down {
  opacity: 1;
  color: #ff0000;
}



